import kebabCase from "lodash.kebabcase"
import React from "react"
import styled from "styled-components"
import { siteMetadata } from "../../../gatsby-config"
import SocialLinks from "../social-link"

const Profile = ({ author, description, siteUrl, keywords, socialLinks }) => {
  const truncatedDescription = truncateText(description, 300)

  return (
    <div>
      <ProfileStyle>
        <Image siteUrl={siteUrl} />
        <Text>
          <Author>{author}</Author>
          <Description>{truncatedDescription}</Description>
          <KeyWordsStyle>
            {keywords.map(keyword => (
              <KeyWordStyle key={kebabCase(keyword)}>
                {/* {kebabCase()} */}
                {keyword}
              </KeyWordStyle>
            ))}
          </KeyWordsStyle>
        </Text>
      </ProfileStyle>

      {/* 소셜 링크 */}
      <SocialLinks socialLinks={socialLinks} />

      {/* 개인 서비스 링크 */}
      <MyServiceLink>
        <MyServiceLinkItem>
          <MyServiceIconImg iconImg={"myc_icon.png"}></MyServiceIconImg>
          <a
            href="https://play.google.com/store/apps/details?id=dev.mhlab.myc"
            target="_blank"
            rel="noopener noreferrer"
            style={{
              marginTop: "5px",
              marginLeft: "10px",
              marginRight: "10px",
              textDecoration: "none",
              color: "#828b93",
              fontSize: "18px",
            }}
          >
            마와셀 (안드로이드)
          </a>
        </MyServiceLinkItem>

        <MyServiceLinkItem>
          <MyServiceIconImg iconImg={"myc_icon.png"}></MyServiceIconImg>
          <a
            href="https://itunes.apple.com/kr/app/apple-store/6474965246"
            target="_blank"
            rel="noopener noreferrer"
            style={{
              marginTop: "5px",
              marginLeft: "10px",
              marginRight: "10px",
              textDecoration: "none",
              color: "#828b93",
              fontSize: "18px",
            }}
          >
            마와셀 (iOS)
          </a>
        </MyServiceLinkItem>

        <MyServiceLinkItem>
          <MyServiceIconImg iconImg={"myc_icon.png"}></MyServiceIconImg>
          <a
            href="https://appmyc.info"
            target="_blank"
            rel="noopener noreferrer"
            style={{
              marginTop: "5px",
              marginLeft: "10px",
              marginRight: "10px",
              textDecoration: "none",
              color: "#828b93",
              fontSize: "18px",
            }}
          >
            마와셀-와인 가격 비교 (웹)
          </a>
        </MyServiceLinkItem>
      </MyServiceLink>
    </div>
  )
}

const truncateText = (text, maxLength) => {
  // \n을 <br /> 태그로 변환
  // const formattedText = text.replace(/\n/g, ",<br />")
  // const formattedText = text.replace(/\n/g, "<br /><br />") // 수정된 부분

  // // HTML 태그를 포함한 전체 길이 체크
  // if (formattedText.length <= maxLength) return formattedText
  // return formattedText.slice(0, maxLength) + "..."

  if (text.length <= maxLength) return text
  return text.slice(0, maxLength) + "..."
}

const ProfileStyle = styled.div`
  display: flex;
  align-items: center;
  padding-bottom: 40px;
`

const Text = styled.div`
  margin-left: 25px;
  width: 60%;
`

const Author = styled.div`
  font-size: 25px;
  color: ${props => props.theme.main.text};
  padding-bottom: 8px;
  font-weight: 600;
`

const Description = styled.div`
  font-size: 16px;
  color: ${props => props.theme.profile.description};
  padding-top: 5px;
  padding-bottom: 5px;
`

const KeyWordsStyle = styled.div`
  margin-top: 30px;
  margin-left: -3px;
  @media ${"screen and (max-width: 768px)"} {
    display: flex;
    flex: 1;
    flex-direction: column;
    margin-top: 10px;
  }
`

const KeyWordStyle = styled.span`
  font-size: 15px;
  margin-right: 13px;
  border-radius: 15px;
  padding: 3px 8px;
  background-color: #484848;
  color: ${props => props.theme.main.text};
  background-color: ${props => props.theme.profile.keyword};
  @media ${"screen and (max-width: 768px)"} {
    margin-top: 10px;
    padding: 10px;
  }
`

const Image = styled.div`
  background-image: url(${siteMetadata.siteUrl}/profile.png);
  width: 140px;
  height: 140px;
  border: 1px solid transparent;
  border-color: white;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
`

const MyServiceLinkItem = styled.div`
  display: flex;
  flex-direction: row;
`

const MyServiceLink = styled.div`
  display: flex;
  flex: 2;
  flex-direction: column;
  justify-content: baseline;
  margin-top: 20px;
`

const MyServiceIconImg = styled.div`
  background-image: url(${props => handel(props.iconImg)});
  width: 30px;
  height: 30px;
  background-size: cover;
  background-position: center;
  border-radius: 20%;
`
const handel = props => {
  return `${siteMetadata.siteUrl}/${props}`
}

export default Profile

